import React, { useRef } from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import Img from 'gatsby-image';
import TransitionLink from 'gatsby-plugin-transition-link';

import getRelatedArticles from './getRelatedArticles';
import relatedArticlesTransition from './relatedArticlesTransition';
import newArticleFadeIn from './newArticleFadeIn';
import fluidImgProps from '../common/fluidImgProps';
import CursorOverlay from '../common/cursorOverlay';

export default ({ currArticle, data }) => {
  if (data === undefined) {
    data = useStaticQuery(graphql`
      {
        craft {
          entries(section: [projects], hidden: false) {
            ... on Craft_Projects {
              slug
              image {
                id
                dateUpdated
                url

                ... on Craft_UploadsVolume {
                  __typename
                  imageFile {
                    childImageSharp {
                      fluid(maxWidth: 600) {
                        src
                        sizes
                        srcSet
                        srcWebp
                        srcSetWebp
                        aspectRatio
                      }
                    }

                    colors {
                      muted
                    }
                  }
                }
              }
              disciplines {
                title
              }
              categories {
                title
              }
            }
          }
        }
      }
    `);
  }

  const allArticles = data.craft.entries;
  const related = getRelatedArticles(currArticle, allArticles);
  const articleTransitionEl = useRef();

  return (
    <>
      <div className="article__contain">
        {related ? (
          <div className="article__related">
            <div className="article__related-title">
              <p className="article__related-title-text fontSize-p3 fontSize-uppercase">
                Related Work
              </p>
            </div>

            <div className="article__related-links">
              {related.map((article, i) => {
                const props = fluidImgProps(article.image[0]);
                const direction = i === 0 ? 'previous' : 'next';

                return (
                  <TransitionLink
                    exit={{
                      zIndex: 2,
                      length: 2,
                      trigger: ({ e }) =>
                        relatedArticlesTransition(articleTransitionEl, direction, e),
                    }}
                    entry={{
                      delay: 1.1,
                      length: 0,
                      trigger: ({ node }) => newArticleFadeIn(articleTransitionEl, node),
                    }}
                    className="hover-cursor"
                    key={article.slug}
                    to={'/work/articles/' + article.slug}
                  >
                    <CursorOverlay>
                      <Img
                        className={`article__related-option -${direction}`}
                        style={{ position: 'absolute' }}
                        {...props}
                      />
                    </CursorOverlay>
                  </TransitionLink>
                );
              })}
            </div>
          </div>
        ) : null}
      </div>
      <div className="article__movingOverlay" ref={articleTransitionEl} />
    </>
  );
};
