const timing = 500;

export default (articleTransitionEl, node) => {
  const articleEl = node.querySelector('.article');

  node.querySelector('nav').classList.add('-transitionHide'); // so hero img begin at top of screen
  articleEl.classList.add('-articleTransition');

  setTimeout(() => {
    const heroMedia = articleEl.querySelector('.article__hero-media');

    for (const placholders of heroMedia.querySelectorAll('div')) {
      placholders.style.backgroundColor = 'unset';
    }

    const heroImg = heroMedia.querySelector('img');
    const styles = articleTransitionEl.current.getAttribute('style');
    const newTransitionEl = articleEl.querySelector('.article__movingOverlay');
    const transitionImgClone = articleTransitionEl.current.children[0].cloneNode(true);

    heroImg.style.opacity = 0;
    newTransitionEl.setAttribute('style', styles);
    newTransitionEl.appendChild(transitionImgClone);
    articleTransitionEl.current.style.opacity = 0;

    const heroImgLoaded = () => {
      heroImg.style.opacity = 1;
      newTransitionEl.style.opacity = 0;
      setTimeout(newTransitionEl.setAttribute('style', ''), timing);
    };

    if (heroImg.complete) {
      heroImgLoaded();
    } else {
      heroImg.addEventListener('load', () => {
        heroImgLoaded();
      });
    }
  }, 50); // give FF 50ms to add hero img
};
