import React from 'react';
import Img from 'gatsby-image';
import fluidImgProps from '../common/fluidImgProps';

export default ({ img }) => {
  const props = fluidImgProps(img);

  return (
    <div className="row article-media">
      <Img className={`article__image-media`} {...props} />
    </div>
  );
};
