import React from 'react';
import Img from 'gatsby-image';

import VimeoEl from '../common/Vimeo';
import fluidImgProps from '../common/fluidImgProps';

export default class Hero extends React.PureComponent {
  state = {
    heroImgLoaded: false,
    heroVideoLoaded: false,
  };

  heroEl = React.createRef();

  heroImgLoaded() {
    setTimeout(() => this.setState({ heroImgLoaded: true }), 400);
  }

  videoLoaded() {
    this.setState({ heroVideoLoaded: true });
  }

  render() {
    let vimeoUrl = false;

    if (this.props.heroBannerVideo !== null) {
      let vimeoSplit = this.props.heroBannerVideo.split('/');

      vimeoUrl = vimeoSplit[vimeoSplit.length - 1];

      // try make weird viemo links work as well as the normal ones
      if (!/^\d+$/.test(vimeoUrl)) {
        vimeoUrl = vimeoSplit[vimeoSplit.length - 2];
      }
    }

    const imgProps = fluidImgProps(this.props.heroImage);

    return (
      <div className="article__hero" ref={this.heroEl}>
        <Img
          className={`
            article__hero-media 
            ${!this.state.heroImgLoaded ? '-zeroWidth' : ''}
            ${this.state.videoLoaded ? '-hidden' : ''}
          `}
          // unset inline styles for '.-zeroWidth' css transition
          imgStyle={{
            top: '',
            left: '',
            transform: '',
            transition: '',
            width: '',
            height: '',
          }}
          {...imgProps}
          onLoad={this.heroImgLoaded.bind(this)}
        />

        {vimeoUrl && (
          <VimeoEl
            className={`vimeo__video article__hero-vimeo`}
            vimeoOptions={{ id: vimeoUrl, loop: true, background: true }}
            style={{ position: 'absolute' }}
            videoPlayingCallback={this.videoLoaded.bind(this)}
          />
        )}
      </div>
    );
  }
}
